import { useLayoutEffect, useRef } from "react";
import styled, { ThemeProvider } from "styled-components";
import { useNavigate } from "react-router-dom";
import openBg from "../Functions/openBg";
import movieImg from "../asset/_home.png"
import movieImg2 from "../asset/__.png"
import BackClickButton from "./BackClickButton";
import closeBg from "../Functions/closeBg";
import theme from "../styles/theme";
import LinkArrow from "./LinkArrow";


const Paper = styled.div`
    position: relative;
    background-color:white;
    width: 100vh;
    max-width: 100%;
    padding: 2rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 12px;
    margin: 2rem auto;
`;
const TitleText = styled.h1`
  font-size:5rem;
  margin-bottom:2rem;
  font-weight:lighter;
`;
const InText = styled.h1`
  word-wrap:break-word;
  margin-bottom:2rem;
  margin-top:2rem;
  font-weight:300;
  line-height:24px;
`;

const MovieImg = styled.img`
  max-width:100%;
  margin:2rem 0 2rem 0;
`;

const SkillBox = styled.div`
  margin-bottom:3rem;
`;

const SkillText = styled.span`
  font-size:1rem;
  border:.5px solid black;
  border-radius:10px;
  padding:1.5px 10px;
  font-weight:lighter;
  margin-right:1rem;
  &:nth-last-child(){
    margin:0;
  }
`;

const Wrap = styled.div`
    background-color:#101e2678;
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    overflow-y: auto;
      @media ${(props) => props.theme.tablet} {
    ${SkillBox} {
      
      display:flex;
      flex-direction: column;
    }
    ${SkillText}{
      border:1px solid black;
      font-size: 2.5vw;
      width:50%;
      margin-bottom:1rem;
      padding: 0.3rem;
      &:nth-last-child(){
        margin:0;
      }
    }
  }
`;

const GoLink = styled.a`
  color:#101e2678 !important ;
  font-size:1vw;
`;

function Section1() {
  const bg = useRef();
  const WrapBg = useRef();
  const back = useNavigate();


  useLayoutEffect(() => {
    // FadeIn 
    openBg(bg);
    WrapBg.current.animate(
      [
        { opacity: "0" },
        { opacity: "1" },
      ],
      {
        delay: 0,
        duration: 700,
        easing: "ease",
        iterations: 1,
      }
    );
  }, []);


  // FadeOut 
  const onClick = () => {
    closeBg(bg);
    WrapBg.current.animate(
      [
        { opacity: "1" },
        { opacity: "0" },
      ],
      {
        delay: 0,
        duration: 700,
        easing: "ease",
        iterations: 1,
      }
    );
    setTimeout(() => {
      back("/About#port");
    }, 500);
  };

  return (
    <ThemeProvider theme={theme}>
      <Wrap ref={WrapBg} >
        <Paper ref={bg}>
          <BackClickButton onClick={onClick} />
          <TitleText>REACT MOVIE APP</TitleText>
          <SkillBox>
            <SkillText>React</SkillText>
            <SkillText>Scss/Sass</SkillText>
            <SkillText>Css Module (Css in Css)</SkillText>
            <SkillText>CRA</SkillText>
          </SkillBox>
          <MovieImg src={movieImg} alt="movieImg" />
          <InText>. .<br /><br />
               React         .<br /><br />
                 API  React          .<br />
          </InText >
          <MovieImg src={movieImg2} alt="movieImg" />
          <InText>           .</InText>
          <InText>    .<br /></InText>
          {/*  */}
          <GoLink href="https://jeonghodong.github.io/react_movie_app/" target="_blank" rel="noreferrer" >
            https://jeonghodong.github.io/react_movie_app/</GoLink>
          <LinkArrow />
        </Paper >
      </Wrap >
    </ThemeProvider>
  );
}



export default Section1;
