import styled from "styled-components";
import { useEffect, useLayoutEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import ContactBox from "../Components/ContactBox";
import SkillBox from "../Components/SkillBox";
import TitleBox from "../Components/TitleBox";
import openBg from "../Functions/openBg";
import PortfolioBox from "../Components/PortfolioBox";
import Header from "../Components/Header";
import closeBg from "../Functions/closeBg";
import RocketButton from "../Components/RocketButton";

const Wrap = styled.div`
  padding-top: 6rem;
  height: 100vh;
  margin: 0 auto;
  width: 100%;
`;

function Intro() {
  const bg = useRef();
  const folio = useRef();
  const skill = useRef();
  const contact = useRef();
  const buttonBg = useRef();
  const title = useRef();
  const rocket = useRef();
  const navigate = useNavigate();

  //  useEffect    ?
  //     useEffect useLayoutEffect          .     useEffect ,    DOM     useLayoutEffect   .
  useLayoutEffect(() => {
    openBg(bg);
  }, []);
  //
  useEffect(() => {
    // window.location.hash   "#"   
    //  hash    hash 
    // html    div id   
    // /About#port  hash  .
    //  useEffect page onMount   hash    href    .
    if (window.location.hash) {
      window.location.href = `${window.location.hash}`;
    }
  }, []);
  //  & About    & PortfolioBox   
  const onClick = (ref, ref2) => {
    //  
    if (ref === "SKILLS") {
      skill.current.scrollIntoView({ behavior: "smooth" });
    }
    if (ref === "WORKS") {
      folio.current.scrollIntoView({ behavior: "smooth" });
    }
    if (ref === "CONTACT") {
      contact.current.scrollIntoView({ behavior: "smooth" });
    }
    if (ref === "ABOUT") {
      bg.current.animate(
        [
          { transform: "translateY(0px)", opacity: "1" },
          { transform: "translateY(200px)", opacity: "0" },
        ],
        {
          delay: 0,
          duration: 700,
          easing: "ease",
          iterations: 1,
          fill: "forwards",
        }
      );
      closeBg(buttonBg);
      setTimeout(() => {
        navigate("/");
      }, 450);
    } else if (ref2 === true) {
      bg.current.animate(
        [
          { transform: "translateY(0px)", opacity: "1" },
          { transform: "translateY(200px)", opacity: "0" },
        ],
        {
          delay: 0,
          duration: 700,
          easing: "ease",
          iterations: 1,
          fill: "forwards",
        }
      );
      closeBg(buttonBg);
      if (ref === 1) {
        setTimeout(() => {
          navigate("/MovieAbout");
        }, 450);
      }
      if (ref === 2) {
        setTimeout(() => {
          navigate("/SnsAbout");
        }, 450)
      };
      if (ref === 3) {
        setTimeout(() => {
          navigate("/PortfolioAbout");
        }, 450);
      }
      if (ref === 4) {
        setTimeout(() => {
          navigate("/PhotographyAbout");
        }, 450);
      }
    }
  };
  // Top 
  const rocketClick = () => {
    rocket.current.animate(
      [
        { transform: "rotate(0deg)" },
        { transform: "translateY(0px)", color: "red" },
        { transform: "rotate(315deg)", color: "red" },
        { transform: "rotate(315deg)", color: "red" },
        { transform: "rotate(315deg)", color: "red" },
        { transform: "rotate(315deg)", color: "red" },
        { transform: "translateY(0px)" },
      ],
      {
        delay: 0,
        duration: 2000,
        easing: "ease",
        iterations: 1,
        fill: "backwards",
      }
    );
    setTimeout(() => {
      title.current.scrollIntoView({ behavior: "smooth" });
    }, 250);
  };
  return (
    <div>
      <Header ref={buttonBg} onClick={onClick} />
      <Wrap ref={bg}>
        <TitleBox ref={title} />
        <SkillBox ref={skill} />
        <PortfolioBox onClick={onClick} ref={folio} />
        <ContactBox ref={contact} />
        <RocketButton ref={rocket} rocketClick={rocketClick} />
      </Wrap>
    </div>
  );
}

export default Intro;
