import { useLayoutEffect, useRef } from "react";
import styled, { ThemeProvider } from "styled-components";
import { useNavigate } from "react-router-dom";
import theme from "../styles/theme";
import openBg from "../Functions/openBg";
import photoImg from "../asset/photo_home.png"
import photoImg1 from "../asset/photo_section.png"
import photoImg2 from "../asset/photo_section2.png"
import BackClickButton from "./BackClickButton";
import closeBg from "../Functions/closeBg";
import LinkArrow from "./LinkArrow";


const Paper = styled.div`
    background-color:white;
    width: 100vh;
    max-width: 100%;
    padding: 2rem;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 8px 12px;
    margin: 2rem auto;
    position:relative;
`;
const TitleText = styled.h1`
  font-size:5rem;
  margin-bottom:2rem;
  font-weight:lighter;
`;
const InText = styled.h1`
  word-wrap:break-word;
  margin-bottom:2rem;
  margin-top:2rem;
  font-weight:300;
  line-height:24px;
`;

const MovieImg = styled.img`
  max-width:100%;
  margin:2rem 0 2rem 0;
`;

const SkillBox = styled.div`
  margin-bottom:3rem;
`;

const SkillText = styled.span`
  font-size:1rem;
  border:.5px solid black;
  border-radius:10px;
  padding:1.5px 10px;
  font-weight:lighter;
  margin-right:1rem;
  &:nth-last-child(){
    margin:0;
  }
`;

const GoLink = styled.a`
  color:#8c6f00a8 !important ;
    font-size:1vw;
`;
const Wrap = styled.div`
    background-color:#8c6f00a8;
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    overflow-y: auto;
                  @media ${(props) => props.theme.tablet} {
    ${SkillBox} {
      display:flex;
      flex-direction: column;
    }
    ${SkillText}{
      border:1px solid black;
      font-size: 2.5vw;
      width:50%;
      margin-bottom:1rem;
      padding: 0.3rem;
      &:nth-last-child(){
        margin:0;
      }
    }
  }
`;
function Section4() {
  const bg = useRef();
  const WrapBg = useRef();
  const back = useNavigate();


  useLayoutEffect(() => {
    // FadeIn 
    openBg(bg);
    WrapBg.current.animate(
      [
        { opacity: "0" },
        { opacity: "1" },
      ],
      {
        delay: 0,
        duration: 1000,
        easing: "ease",
        iterations: 1,
      }
    );
  }, []);

  // FadeOut 
  const onClick = () => {
    closeBg(bg);
    WrapBg.current.animate(
      [
        { opacity: "1" },
        { opacity: "0" },
      ],
      {
        delay: 0,
        duration: 700,
        easing: "ease",
        iterations: 1,
      }
    );
    setTimeout(() => {
      back("/About#port");
    }, 500);
  };

  return (
    <ThemeProvider theme={theme}>
      <Wrap Wrap ref={WrapBg} >
        <Paper ref={bg}>
          <BackClickButton onClick={onClick} />
          <TitleText>Photography Site</TitleText>
          <SkillBox>
            <SkillText>HTML</SkillText>
            <SkillText>CSS</SkillText>
            <SkillText>Javascript</SkillText>
            <SkillText>Media Query</SkillText>
          </SkillBox>
          <MovieImg src={photoImg} alt="ChartImg" />
          <InText>
            . .<br /><br />
            HTML, CSS, JS   Figma    .<br /><br />
              HTML, CSS       Photography Site  .</InText>
          <MovieImg src={photoImg1} alt="ChartImg" />
          <InText>
            Figma          CSS   .<br /><br />
            float, flex, grid, position    .
          </InText>
          <MovieImg src={photoImg2} alt="ChartImg" />
          {/*  */}
          <LinkArrow />
          <GoLink href="https://jeonghodong.github.io/photography_site_figma_clone/" target="_blank" rel="noreferrer" >
            https://jeonghodong.github.io/photography_site_figma_clone/</GoLink>
        </Paper>
      </Wrap >
    </ThemeProvider  >
  );
}

export default Section4;
